import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
import uniCollapse from '@/components/uni-collapse/uni-collapse.vue';
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue';
import lcNumberBox from '@/components/lc-number-box/lc-numberBox.vue';
export default {
	components: {
		uniCollapse,
		uniCollapseItem,
		lcNumberBox,
		mSearch
	},
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			cateList: [],
			goodsList: [],
			myCarts: {},
			slist: [],
			index: 0,
			searchName: '',
		}
	},
	// onLoad() {
	// 	this.loadData();
	// },
	computed:{
		cartNum() {
			return this.$store.state.number
		}
	},
	onShow() {
		this.loadData();
		this.number = 0;
		this.$store.state.number = 0;
	},
	methods: {
		changeMyCarts(e) {
			this.number = 1;
			this.$api.account.editMyCart(e.id, this.number).then(data => {
				this.$store.commit('addNme',1);
				this.$api.msg('添加成功');
			});
			// 	this.$store.commit('addNme',1);
			// this.$api.account.editMyCart(good.id, e);
			// this.$api.msg('添加成功');	
		},
		async loadData() {
			await this.$api.commodity.getCate(666).then(data => {
				this.cateList = data
			})
			await this.$api.commodity.getGoods().then(data => {
				this.goodsList = data
			})
			this.loadCart()
		},
		async loadCart() {
			var obj = new Object()
			await this.$api.account.getMyCart().then(res => {
				res.forEach(item => {
					obj[item['goods_id']] = item['amount']
				})
				this.myCarts = obj
			});
		},
		getCart(good) {
			return this.myCarts.hasOwnProperty(good['id']) ? this.myCarts[good['id']] : 0
		},
		async cateGoods(cate_id, idx) {
			this.index = idx;
			this.goodsList = await this.$api.commodity.getGoods({
				'cate_id': cate_id
			});
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		search(e, val) {
			this['val' + val] = e;
			uni.navigateTo({
				url: '/pages/search/search?search=' + e
			})
		}
	}
}
